<style>.skill-line {
    margin-bottom: 0.5pt;
}

.skill-value, .skill-name {
    font-family: 'Gothic A1', sans-serif;
    font-size: 16pt;
    border-bottom-width: 0.5pt;
}

.skill-value {
    width: 2.2rem;
    margin-right: 3pt;
    font-weight: bolder;
}

input[type="number"],
input[type="number"]:active,
input[type="number"]:hover,
input[type="number"]:focus,
input[type="text"],
input[type="text"]:active,
input[type="text"]:hover,
input[type="text"]:focus {
    border-radius: 0;
    border: 0.5pt solid white;
    border-bottom-color: black;
}

.skill-name {
    width: 70%;
    border-bottom-color: #666;
}

@media print {
    .skill-name {
        border-color: white !important;
    }
}</style>

<script>
    import Section from '../../shared/Section';
    export let skills=[
        {name:"Academics",value:0},
        {name:"Athletics",value:0},
        {name:"Burglary",value:0},
        {name:"Contacts",value:0},
        {name:"Crafts",value:0},
        {name:"Deceive",value:0},
        {name:"Drive",value:0},
        {name:"Empathy",value:0},
        {name:"Fight",value:0},
        {name:"Investigate",value:0},
        {name:"Lore",value:0},
        {name:"Notice",value:0},
        {name:"Physique",value:0},
        {name:"Provoke",value:0},
        {name:"Rapport",value:0},
        {name:"Resources",value:0},
        {name:"Shoot",value:0},
        {name:"Stealth",value:0},
        {name:"Will",value:0}
    ];

    let skillsHint="Start with one Great (+4), two Good (+3), three Fair (+2), four Average (+1), rest at Mediocre (=0)"
</script>

<Section title="Skills" hint="{skillsHint}">

    {#each skills as skill}
        <div class="skill-line">
            <span>+</span>
            <input maxlength="2" type="number" class="skill-value" min="0" max="10" bind:value={skill.value}/>
            <input type="text" class="skill-name" bind:value={skill.name}/>
        </div>
    {/each}

</Section>
