<style>.toc {
    padding: 0.5rem;
    float: left;
    position: fixed;
    top:3.5rem;
    left:0;
    width:auto;
    max-width: 20rem;
    height: calc(100vh - 3.5rem);
    overflow: auto;
    background-color: var(--mdc-theme-surface);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.page-content {
    height: calc(100vh - 6rem);
    overflow: auto;
    margin:0;
}

.this-page {
    width: 8.5in;
    height: auto;
    padding: 0.5in;
    margin: 0.5in auto;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    box-sizing: border-box;
}</style>

<script>
    import '@material/mwc-icon-button'

    import TOC from './TOC.md'
    import Srd from './SrdContent.md'

    let showToc= window.innerWidth > 15.25*6.022*16; //(inches*em/inch*px/em)

    if (window.location.hash) {
        setTimeout(() => {
            document.getElementById(window.location.hash.replace("#","")).scrollIntoView();
        }, 250);
    }
</script>


    <div class="toc">

        {#if showToc}
            <mwc-icon-button style="float:right" icon="chevron_left" on:click={()=> showToc=false} ></mwc-icon-button>
        {:else}
            <mwc-icon-button icon="chevron_right" on:click={()=> showToc=true} ></mwc-icon-button>
        {/if}

        {#if showToc}
            <h2>Table of Contents</h2>
                <TOC />
        {/if}
    </div>
<div class="this-page">
    <Srd />
</div>



